.shop {
  min-height: 100vh;
  background-color: #7fffd4;
  border: 1px solid red;
  grid-template-rows: auto auto 9rem;
  grid-template-columns: auto;
  display: grid;
}

.login {
  background-color: #7fffd4;
  border: 1px solid red;
  grid-area: 1 / 1 / 2 / 2;
}

.product-grid {
  background-color: #fff;
  border: 1px solid red;
  flex-flow: wrap;
  grid-area: 2 / 1 / 3 / 2;
  gap: 10px 20px;
  display: flex;
}

.products {
  background-color: #fff;
  border: 1px solid red;
}

.shoppingcart {
  background-color: #7fffd4;
  border: 1px solid red;
  grid-area: 3 / 1 / 4 / 2;
}

.fa-stack[data-count]:after {
  content: attr(data-count);
  color: #7fffd4;
  color: #000;
  text-align: center;
  min-width: 2em;
  background: #7fffd4;
  border-style: solid;
  border-radius: 999px;
  padding: .6em;
  font-size: 40%;
  font-weight: bold;
  line-height: .75em;
  position: absolute;
  top: 0%;
  right: 0%;
}

